import { VhQueryCafe, VhAlgorithm, VhQuery } from 'ionic-vhframeworks';
import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { LanguageService } from 'src/app/cafe/services/language.service';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';

@Component({
  selector: 'app-detail-voucher',
  templateUrl: './detail-voucher.component.html',
  styleUrls: ['./detail-voucher.component.scss'],
})
export class DetailVoucherComponent implements OnInit {
  public editVoucherForm: FormGroup; // form chỉnh sửa voucher
  public list_barcode: any; // danh sách barcode
  public dataRestore: any =
    this.router.getCurrentNavigation().extras.state.dataRestore;
  public data: any; // voucher được chọn
  public show: any[] = []; // Danh sách sản phẩm trong voucher
  public heightScroll: string;
  list_branch: any[];
  price_origin: any;
  total: number;
  editPrice: any = {};
  constructor(
    private router: Router,
    public languageService: LanguageService,
    public vhQueryCafe: VhQueryCafe,
    public vhAlgorithm: VhAlgorithm,
    private vhQuery: VhQuery,
    private vhComponent: VhComponent,
    private cdRef: ChangeDetectorRef
  ) {
    this.data = this.router.getCurrentNavigation().extras.state.data;
  }

  ngOnInit(): void {
    let localProducts: Array<any> = this.vhQueryCafe.getlocalDetailProducts();
    this.list_branch = this.vhQueryCafe.getlocalBranchs();
    this.getBarcodes();
    this.initForm(this.data);
    if (this.data.products) {
      this.total = this.data.units[0].price;
      this.data.products.forEach((product, index) => {
        let localProduct = product.id_subproduct
          ? localProducts.find(
              (find) => find.id_subproduct == product.id_subproduct
            )
          : localProducts.find((find) => find._id == product.id_product);
        this.show.push({
          ...product,
          price_origin: product.price,
          index: index,
          name: localProduct.name,
          img: localProduct.img,
          unit: this.vhQueryCafe.getUnit_byRatio(
            localProduct.units,
            product.ratio
          ).unit,
        });
      });
    }

    this.vhAlgorithm.waitingStack().then(() => {
      this.renderPrice(this.data.products);
    });
  }

  ngAfterViewChecked() {
    if (document.querySelector('.voucher-products'))
      this.heightScroll =
        document.querySelector('.voucher-products').clientHeight -
        document.querySelector('.ant-table-thead').clientHeight +
        'px';

    this.cdRef.detectChanges();
  }
  ngAfterViewInit() {
    this.vhAlgorithm.waitingStack().then(() => {
      this.price_origin = this.vhAlgorithm.vhnumeral('.price_origin');
    });
  }

  /**
   * gán các giá trị của voucher vào form để xử lý
   */
  initForm(item) {
    this.editVoucherForm = new FormGroup({
      _id: new FormControl(item._id),
      name: new FormControl(item.name, [Validators.required]),
      id_design_barcode: new FormControl(item.id_design_barcode),
      points: new FormControl(item.units[0].points, [Validators.required]),
      unit: new FormControl(
        item.units[0].unit,
        Validators.compose([Validators.required])
      ),
      price: new FormControl(
        item.units[0].price,
        Validators.compose([Validators.required])
      ),
      price_origin: new FormControl(
        item.units[0].price_origin,
        Validators.compose([Validators.required])
      ),
      products: new FormControl(item.products),
      type: new FormControl(item.type),
      id_branch: new FormControl(item.id_branch, [Validators.required]),
    });
  }

  goBack() {
    this.router.navigate([
      'cafe/dashboard/management/sales-program/voucher/list-voucher',
    ]);
  }

  /**
   * get mẫu thiết kế voucher
   */
  getBarcodes() {
    this.vhQuery
      .getDocsByFields("barcode_designs", {
        id_branch: { $eq: this.vhQueryCafe.getDefaultBranch()._id },
        type: { $eq: 3 },
      })
      .then((response: any) => {
        this.list_barcode = response.map((item) => {
          item.property = this.vhAlgorithm.sortNumberbyASC(
            item.property,
            'index'
          );
          return { ...item };
        });
      });
  }

  /**
   * bắt sk thay đổi giá niêm yết của voucher(giá gốc voucher)
   */
  changePriceForm() {
    this.editVoucherForm.controls['price_origin'].setValue(
      parseFloat(this.price_origin.getRawValue())
    );
    console.log(this.editVoucherForm.value.price_origin);
  }
  /**
   * cập nhật voucher theo trường truyền vào
   */
  editVoucher(field: string, object) {
    if (
      this.editVoucherForm.valid &&
      parseFloat(this.price_origin.getRawValue()) >= this.total
    ) {
      this.vhQueryCafe
        .updateVoucher(this.editVoucherForm.value._id, object)
        .then(
          (bool) => {
            console.log('updateVouchers', bool);
          },
          (error) => {
            this.vhComponent.alertMessageDesktop('error', error);
          }
        );
    } else {
      this.vhComponent.alertMessageDesktop(
        'error',
        this.languageService.translate(
          'Giá niêm yết phải lớn hơn hoặc bằng giá bán'
        )
      );
    }
  }

  /**
   * xóa voucher khỏi DB
   */
  deleteVoucher() {
    this.vhComponent.showLoading('', 'transparent-loading').then(() => {
      this.vhQueryCafe.deleteVoucher(this.data._id).then(
        (rsp: any) => {
          this.vhComponent.hideLoading(0);
          if (rsp.vcode == 0) {
            this.vhComponent.alertMessageDesktop(
              'success',
              this.languageService.translate('Xóa Voucher thành công')
            );
            this.goBack();
          } else if (rsp.vcode == 1) {
            this.vhComponent.alertMessageDesktop(
              'error',
              this.languageService.translate(
                'Xóa Voucher không thành công, vui lòng xóa nó khỏi các đợt phát hành liên quan!'
              )
            );
          }
        },
        (error) => {
          this.vhComponent.hideLoading(0);
          console.log('error', error);
        }
      );
    });
  }

  /**
   * cập nhật trường products và units của voucher
   */
  updateVoucherByField(value) {
    this.vhQueryCafe.updateVoucher(this.editVoucherForm.value._id, value).then(
      (bool) => {
        console.log('updateVouchers', bool);
      },
      (error) => {
        this.vhComponent.alertMessageDesktop('error', error);
      }
    );
  }

  /**
   * tính lại tổng tiền của sp trong voucher
   */
  cardPrice() {
    this.total = 0;
    this.show.forEach((element) => {
      this.total = this.total + element.price * element.quantity;
    });
    this.updateVoucherByField({
      products: this.show,
      units: [
        {
          price: this.total,
          default: true,
          ratio: 1,
          points: this.editVoucherForm.value.points,
          price_origin: this.total * 1.1,
          unit: this.editVoucherForm.value.unit,
        },
      ],
    });
  }
  /**
   * bắt sk thay đổi giá của sp trong voucher
   */
  changePriceItemCard(item, index) {
    if (item) {
      let price: string = this.editPrice['product-' + index].getRawValue();
      item.price = price ? parseFloat(price) : item.price_origin;
    }
    this.cardPrice();
  }

  /**
   * tạo cleavejs cho input giá của sp
   */
  renderPrice(arr: any[]) {
    arr.forEach((item, index) => {
      this.editPrice['product-' + index] = this.vhAlgorithm.vhnumeral(
        '.product-' + index
      );
    });
  }
}
